.btn-outline-text{
  color:white!important;
  border-color:white !important;
}

.btn-outline-text:hover {
  color: #fa5d32 !important;
  background-color: rgb(219, 219, 219);
  border-color: white;
}

.btn-outline-text:focus, .btn-outline-text.focus {
  box-shadow: 0 0 0 0.2rem rgba(240, 240, 240, 0.5);
}

.btn-outline-text.disabled, .btn-outline-text:disabled {
  color: white;
  background-color: transparent;
}

.btn-outline-text:not(:disabled):not(.disabled):active, .btn-outline-text:not(:disabled):not(.disabled).active,
.show > .btn-outline-text.dropdown-toggle {
  color: #fff;
  background-color: rgb(192, 188, 188) !important;
  border-color: white;
}

.btn-outline-text:not(:disabled):not(.disabled):active:focus, .btn-outline-primary:not(:disabled):not(.disabled).active:focus,
.show > .btn-outline-primary.dropdown-toggle:focus {
  box-shadow: 0 0 0 0.2rem rgba(243, 243, 243, 0.5);
}
  
