.btn-outline-orange{
color: #fa5d32!important;
border-color: #fa5d32 !important;
}

.btn-outline-orange:hover {
color: #fa5d32 !important;
background-color: rgb(219, 219, 219);
border-color:  #fa5d32;
}

.btn-outline-orange:focus, .btn-outline-orange.focus {
box-shadow: 0 0 0 0.2rem rgba(240, 240, 240, 0.5);
}

.btn-outline-orange.disabled, .btn-outline-orange:disabled {
color:  #fa5d32;
background-color: transparent;
}

.btn-outline-orange:not(:disabled):not(.disabled):active, .btn-outline-orange:not(:disabled):not(.disabled).active,
.show > .btn-outline-orange.dropdown-toggle {
color: #fff;
background-color: rgb(192, 188, 188) !important;
border-color: #fa5d32;
}

.btn-outline-orange:not(:disabled):not(.disabled):active:focus, .btn-outline-primary:not(:disabled):not(.disabled).active:focus,
.show > .btn-outline-primary.dropdown-toggle:focus {
box-shadow: 0 0 0 0.2rem rgba(243, 243, 243, 0.5);
}
    
